'use strict';

module.exports = function(app){

    require('./styles.css');

	app.controller( 'newsCtrl', [ 'textAngularManager', 'NewsFactory',
                                    require('./newsCtrl') ]);
    app.factory( 'NewsFactory', [ '$http',
                                    require('./NewsFactory') ]);

    app.config([ '$stateProvider', function($stateProvider){

        $stateProvider.state('news', {
            url: '/news',
            template: require('./news.html'),
            data: {
            	label  : "Новости",
                parent : "dashboard"
            },
            controller: 'newsCtrl as vm'
        });

    } ]);

};


