'use strict';

module.exports = function ListsValuesFactory($http){

	var lists_values = JSON.parse( 
		`[
			{
				"id":1,
				"name":"HDD",
				"created_at":null,
				"updated_at":null
			}
		]`
 		),

		values = JSON.parse(`
			[
				{
					"id":1,
					"list_value_id":1,
					"name":"128 GB",
					"created_at":null,
					"updated_at":null
				},
				{
					"id":2,
					"list_value_id":1,
					"name":"256 GB",
					"created_at":null,
					"updated_at":null
				}
			]
		`);


	function get() {

		return lists_values;
	}

	function getList(id) {
		
		function catchValuesById(val) {
			return val.list_value_id === id;
		}

		return( values.filter( catchValuesById )  );
	}



	return {

		get     : get,
		getList : getList

	}

};


