"use strict";

module.exports = function(app){

	require('./styles.css');
    require('./create')(app);
    require('./editCategory')(app);
    require('./deleteCategory')(app);
	
	app.controller( 'categoriesCtrl', [ '$scope', '$timeout', '$state', '$stateParams', '$rootScope', 'CategoriesFactory',
                                        require('./categoriesCtrl') ]);

    app.config([ '$stateProvider', function($stateProvider){

        $stateProvider.state('categories', {
            url: '/categories?subcategory',
            template: require('./categories.html'),
            data: {
                label: "Категории",
                parent: "dashboard"
            },
            controller: 'categoriesCtrl as vm'
        });

    } ]);

};


