'use strict';

module.exports = function list_valuesCtrl( $stateParams, ListsValuesFactory ){

	var vm = this,
		idList = parseInt($stateParams.id);

	vm.newValue = {};

	vm.list_values = ListsValuesFactory.getList( idList );
    
};


