'use strict';

module.exports = function($timeout, profileFactory){

    var vm = this;

	vm.profile = profileFactory.getProfile();

	$timeout( function() {
		$('.dropdown-button').dropdown({
			inDuration: 300,
			outDuration: 225,
			belowOrigin: true,
			alignment: 'right'
		});
	} );

};


